import { ProductsModule } from './products/products.module';
import { DashboardComponent } from './dashboard.component';

import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WeeklyGamesComponent } from './weekly-games/weekly-games.component';



const routes: Routes = [
  
  {
    path: '',
    component: DashboardComponent,
    // canActivateChild : [AuthGuard],
    children: [
      {
        path: '',
        redirectTo:'weekly-games'

      },   
      {
        path : 'weekly-games',
        loadChildren: () => import('./weekly-games/weekly-games.module').then(m => m.WeeklyGamesModule),
      },
      {
        path : 'products',
        loadChildren: () => import('./products/products.module').then(m => m.ProductsModule),
      },
      {
        path : 'web-interface',
        loadChildren: () => import('./web-interface/web-interface.module').then(m => m.WebInterfaceModule),
      },
      {
        path : 'winner',
        loadChildren: () => import('./winner/winner.module').then(m => m.WinnerModule),
      },
      {
        path : 'payment',
        loadChildren: () => import('./payment/payment.module').then(m => m.PaymentModule),
      },
      {
        path : 'judgement',
        loadChildren: () => import('./judgement/judgement.module').then(m => m.JudementModule),
      },
      {
        path : 'judge-list',
        loadChildren: () => import('./judge-list/judgelist.module').then(m => m.JudgelistModule),
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }
